import React, { useState, useEffect } from "react";
import {
  requestRoninWalletConnector,
  ConnectorError,
} from "@sky-mavis/tanto-connect";
import "./styles.css";

function PreRegistrationRonin() {
  const [connector, setConnector] = useState(null);
  const [connectedAddress, setConnectedAddress] = useState(null);
  const [email, setEmail] = useState("");
  const [isPreRegistered, setIsPreRegistered] = useState(false);
  const [error, setError] = useState(null);

  // Inicializa el conector al montar
  useEffect(() => {
    (async () => {
      try {
        const conn = await requestRoninWalletConnector();
        setConnector(conn);
      } catch (err) {
        if (err instanceof ConnectorError) {
          setError(err.name);
        } else {
          setError("Error initializing connector.");
        }
      }
    })();
  }, []);

  // Verifica pre-registro cuando cambia la direccin conectada
  useEffect(() => {
    if (connectedAddress) {
      const key = `preRegisteredRonin_${connectedAddress}`;
      setIsPreRegistered(localStorage.getItem(key) === "true");
    }
  }, [connectedAddress]);

  const handleConnectAndRegister = async (e) => {
    e.preventDefault();
    setError(null);

    if (!email) {
      setError("Please enter a valid email.");
      return;
    }

    // Conecta la wallet si no hay direccin
    let account = connectedAddress;
    if (!account) {
      if (!connector) return;
      try {
        const result = await connector.connect();
        if (result) {
          account = result.account;
          setConnectedAddress(account);
        }
      } catch (err) {
        console.error(err);
        setError("Error connecting to Ronin Wallet.");
        return;
      }
    }

    const key = `preRegisteredRonin_${account}`;
    if (localStorage.getItem(key) === "true") {
      alert("This wallet has already been pre-registered.");
      return;
    }

    // Guardar pre-registro en localStorage
    localStorage.setItem(key, "true");
    setIsPreRegistered(true);
    alert(`Pre-registered ${email} with wallet ${account} successfully!`);
  };

  const handleDisconnect = () => {
    setConnectedAddress(null);
    setIsPreRegistered(false);
    setError(null);
  };

  return (
    <div className="pre-registration-container">
      <h2 className="h2xd">Pre-register for CBT</h2>
      {error && <p className="error">{error}</p>}
      <form onSubmit={handleConnectAndRegister} className="pre-reg-form">
        <label htmlFor="email">Email Address</label>
        <input
          id="email"
          type="email"
          className="email-input"
          placeholder="you@example.com"
          value={email}
          onChange={(e) => setEmail(e.target.value)}
          required
        />

        {connectedAddress && (
          <p className="wallet-info">Connected: {connectedAddress}</p>
        )}

        <button
          type="submit"
          className="button-custom"
          disabled={isPreRegistered}
        >
          {isPreRegistered ? "Already Pre-Registered" : "Pre-Register"}
        </button>

        {connectedAddress && (
          <button
            type="button"
            className="button-custom disconnect"
            onClick={handleDisconnect}
          >
            Disconnect
          </button>
        )}
      </form>
    </div>
  );
}

export default PreRegistrationRonin;